import Vue from 'vue';
import Router from 'vue-router';

Vue.use(Router);

const routes = [
  {
    path: '*',
    redirect: '/shopping'
  },
  {
    name: 'orders',
    component: () => import('./view/orders'),
    meta: {
      title: 'Orders'
    }
  },
  {
    name: 'test',
    component: () => import('./view/test'),
    meta: {
      title: 'TEST'
    }
  },
  {
    name: 'contact',
    component: () => import('./view/contact'),
    meta: {
      title: 'CONTACT'
    }
  },
  {
    name: 'chat',
    component: () => import('./view/chat'),
    meta: {
      title: 'CHAT'
    }
  },
  {
    name: 'superb',
    component: () => import('./view/superb'),
    meta: {
      title: 'SUPBERB'
    }
  },
  {
    name: 'detail',
    component: () => import('./view/shopping/detail'),
    meta: {
      title: 'DETAIL'
    }
  },
  {
    name: 'shopping',
    component: () => import('./view/shopping'),
    meta: {
      title: 'SHOPPING'
    }
  },
  {
    name: 'info',
    component: () => import('./view/info'),
    meta: {
      title: 'Personal Infomation'
    }
  },
  {
    name: 'name',
    component: () => import('./view/name'),
    meta: {
      title: 'Enter Name'
    }
  },
  {
    name: 'bday',
    component: () => import('./view/bday'),
    meta: {
      title: 'Enter Your Birthday'
    }
  },
  {
    name: 'gender',
    component: () => import('./view/gender'),
    meta: {
      title: 'Enter Your Gender'
    }
  },
  {
    name: 'shipping',
    component: () => import('./view/shipping'),
    meta: {
      title: 'Enter Your Shipping Address'
    }
  },
  {
    name: 'billing',
    component: () => import('./view/billing'),
    meta: {
      title: 'Enter Your Billing Address'
    }
  },
  {
    name: 'share',
    component: () => import('./view/share'),
    meta: {
      title: 'Share Your Purchase'
    }
  }
];

// add route path
routes.forEach(route => {
  route.path = route.path || '/' + (route.name || '');
});

const router = new Router({ routes });

router.beforeEach((to, from, next) => {
  const title = to.meta && to.meta.title;
  if (title) {
    document.title = title;
  }
  next();
});

export {
  router
};
